import { Plugin } from '@ali/4ever-cangjie';
import { HeadingGroupPlugin } from '@ali/4ever-bamboo';
import { HeadingGroupConfig } from '../types';

const { createAutoUnfold } = HeadingGroupPlugin.handlers;
const { toggleFold } = HeadingGroupPlugin.actions;

function createOnCangjieSelect(config: HeadingGroupConfig): Plugin['onCangjieSelect'] {
  const { autoUnfold = false } = config;

  const handleAutoUnfold = createAutoUnfold({
    onAutoUnfold: (controller, node) => controller.run('onAction', toggleFold({
      node,
      fold: false,
    })),
    inject: true,
  });

  return function onCangjieSelect(event, controller, next) {
    if (autoUnfold) {
      handleAutoUnfold(event, controller);
    }
    return next();
  };
}

export default createOnCangjieSelect;
